@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-semi-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-semi-bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-bold-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold-italic.woff") format("woff");
  font-weight: 700;
  font-style: italic;
  font-display: swap;
}

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0 none;
  clip-path: inset(100%);
}

.accordion {
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  color: #1d1613;
  border: 1px solid #a87b62;
}

.accordion__item {
  position: relative;
  cursor: pointer;
}

.accordion__item:hover {
  background-color: #f0eae4;
}

.accordion__item:not(:last-child) {
  border-bottom: 1px solid #a87b62;
}

.accordion__title svg {
  position: absolute;
  width: 17px;
  height: 10px;
  stroke: #a87b62;
  fill: none;
}

.accordion__title--current svg {
  transform: rotate(180deg);
}

.accordion__content {
  line-height: 130%;
}

.accordion__content--hidden {
  display: none;
}

.accordion__current .accordion__content {
  display: block;
}

.article {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 300px;
}

.article time {
  display: block;
  margin-bottom: 12px;
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  color: #9b9997;
}

.article h3 {
  margin: 0 auto 39px;
  width: 57%;
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  font-weight: 600;
  font-size: 26px;
  text-align: center;
  line-height: 33px;
}

.article a {
  margin: 0 auto;
}

.article img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.article--large {
  position: relative;
  display: block;
  height: 630px;
}

.article--large img {
  height: 630px;
}

.article__column {
  padding-top: 67px;
  height: 300px;
  background-color: #f0eae4;
  text-align: center;
}

.article--large .article__column {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 50%;
}

.article__image-block {
  overflow: hidden;
}

.blog h2 {
  margin-top: 0;
  margin-bottom: 20px;
}

.blog ul {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 2.56%;
  row-gap: 30px;
  padding-left: 0;
}

.blog li:nth-child(3) {
  grid-column: 2/-1;
  grid-row: 1/3;
}

.blog__link {
  margin: 0 auto 64px;
}

.button {
  width: 100%;
  height: 68px;
  font-size: 16px;
  font-weight: 500;
  color: #ffffff;
  background-color: #a87b62;
  border-style: none;
  cursor: pointer;
}

.button:hover {
  background-color: #f0eae4;
  opacity: 1px solid #a87b62;
  color: #a87b62;
}

.button:active {
  opacity: 0.5;
}

.button--transparent {
  background-color: #ffffff;
  color: #1d1613;
  border: 1px solid #a87b62;
}

.button--transparent:hover {
  background-color: #a87b62;
  color: #ffffff;
}

.card-product {
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
}

.card-product--hidden {
  display: none;
}

.card-product img {
  margin-bottom: 13px;
  width: 100%;
  object-fit: cover;
}

.card-product h3 {
  margin-top: 0;
  margin-bottom: 5px;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  text-align: center;
}

.card-product p {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 130%;
  text-align: center;
}

.card-product a {
  color: inherit;
}

.card-product a:visited {
  color: inherit;
}

.card-product a:hover {
  opacity: 0.5;
  color: rgba(0, 0, 0, 0.5);
}

.card-product a:active {
  opacity: 0.2;
}

.catalog {
  position: relative;
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
  row-gap: 39px;
  padding-left: 0;
  margin-bottom: 93px;
  width: 100%;
  min-height: 589px;
}

.catalog__list li {
  width: 100%;
}

.catalog__list img {
  width: 100%;
  height: 272px;
  object-fit: cover;
}

.catalog__control {
  position: absolute;
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #1d1613;
}

.catalog__control--previous {
  left: 0;
}

.catalog__control--next {
  right: 0;
}

.catalog__pagination {
  position: absolute;
  left: 50%;
  width: max-content;
}

.catalog__pagination ul {
  padding-left: 0;
  margin-bottom: 0;
}

.control {
  padding: 0;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

.control:disabled {
  opacity: 0.2;
}

.control--nojs {
  display: none;
}

.faq h2 {
  margin-top: 0;
  margin-bottom: 48px;
}

.faq ul {
  margin: 0 auto;
  padding-left: 0;
  width: 83%;
}

.faq li {
  padding-bottom: 41px;
}

.faq h3 {
  margin-top: 0;
  margin-bottom: 0;
  padding: 43px 43px 0;
  font-size: 18px;
  font-weight: 500;
  line-height: 140%;
}

.faq p {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 8px;
  padding-left: 48px;
  padding-right: 48px;
  width: 62%;
}

.faq svg {
  top: 48px;
  right: 50px;
}

.filter {
  font-size: 16px;
  font-weight: 500;
}

.filter fieldset {
  margin: 0;
  padding: 0 0 29px 0;
  border-top: none;
  border-left: none;
  border-right: none;
}

.filter fieldset:last-child {
  border-bottom: none;
}

.filter legend {
  padding: 21px 25px 0;
  width: 100%;
  height: 68px;
}

.filter svg {
  top: 27px;
  right: 24px;
}

.filter__checkbox-container {
  margin: 0;
  padding-left: 34px;
  font-size: 14px;
}

.filter__checkbox-container li:not(:last-child) {
  margin-bottom: 12px;
}

.filter__checkbox-container input {
  appearance: none;
  margin: 2px 10px 0 0;
  width: 15px;
  height: 15px;
  border: 1px solid #a87b62;
}

.filter__checkbox-container input:hover {
  border-color: #1d1613;
}

.filter__checkbox-container input:checked {
  background-color: #a87b62;
}

.filter__checkbox-container input:hover + span {
  color: #a87b62;
}

.filter__checkbox-container input:active + span {
  opacity: 0.5;
}

.filter__checkbox-container label {
  display: flex;
  cursor: pointer;
}

.filter__accordion {
  margin-bottom: 33px;
}

.filter__submit {
  margin-bottom: 27px;
}

.filter__button {
  display: none;
}

.filter__close {
  display: none;
}

.filter__close svg {
  fill: #ffffff;
}

.form {
  position: relative;
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
}

.form input {
  width: 100%;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  border-style: none;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  background-color: transparent;
}

.form input::-webkit-input-placeholder {
  color: #9b9997;
}

.form input::-moz-placeholder {
  color: #9b9997;
}

.form input:hover {
  opacity: 0.5;
}

.form input:focus {
  outline: 3px solid #a87b62;
}

.form--black input {
  border-color: #1d1613;
  color: #1d1613;
}

.gallery h2 {
  margin-top: 0;
  margin-bottom: 31px;
}

.gallery ul {
  display: grid;
  grid-template-columns: 19.6% 34% repeat(2, 19.6%);
  column-gap: 27px;
  row-gap: 32px;
  padding-left: 0;
  margin-bottom: 0;
}

.gallery__link {
  margin: 0 auto 63px;
}

.gallery__item {
  width: 100%;
  height: 230px;
}

.gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gallery__item--large {
  grid-row: 1/3;
  grid-column: 2/3;
  height: 412px;
}

.gallery__item--small {
  height: 150px;
}

.intro-catalog {
  padding-top: 94px;
  min-height: 261px;
  text-align: center;
  background-image: url("../img/intro/intro-catalog-desktop.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  color: #ffffff;
}

.intro-catalog p {
  margin-top: 0;
  margin-bottom: 4px;
  font-weight: bold;
  font-size: 34px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

.intro-catalog span {
  display: block;
  font-size: 22px;
  font-weight: 500px;
  font-style: italic;
  text-align: center;
}

.intro {
  padding-top: 181px;
  min-height: 463px;
  text-align: center;
  background-image: url("../img/intro/intro-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  color: #ffffff;
}

.intro p {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: bold;
  font-size: 45px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

.intro span {
  display: block;
  font-size: 38px;
  font-style: italic;
  text-align: center;
}

.link {
  display: block;
  width: max-content;
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  border-bottom: 1px solid #a87b62;
}

.link:visited {
  color: inherit;
}

.link:hover {
  color: #a87b62;
}

.link:active {
  color: inherit;
  opacity: 0.5;
}

.logo {
  display: block;
  width: 66px;
  height: 66px;
}

.logo:hover {
  fill: #a87b62;
}

.logo:active {
  opacity: 0.5;
}

.main-navigation {
  padding-top: 22px;
  border-top: 1px solid #f6f5f4;
}

.main-navigation__menu {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-left: 111px;
  padding-right: 98px;
  width: 100%;
  max-width: 1366px;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
}

.main-navigation__menu li {
  margin-bottom: 10px;
}

.main-navigation__menu li:not(:last-child) {
  margin-right: 35px;
}

.main-navigation__about-company,
.main-navigation__login {
  display: none;
}

.main-navigation__login {
  width: max-content;
}

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 430px;
  height: 586px;
  background-color: #f0eae4;
  z-index: 100;
}

.modal::before {
  content: "";
  display: block;
  width: 100%;
  height: 138px;
  margin-bottom: 49px;
  background-image: url("../img/intro/intro-login.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.modal--page {
  position: static;
  margin: 0 auto;
  height: 100%;
  background-color: transparent;
}

.modal h2 {
  margin-top: 0;
  margin-bottom: 34px;
  text-align: center;
}

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 14px;
  height: 14px;
  border-style: none;
  background-color: transparent;
  background-image: url("../img/icon/close.svg");
  background-size: cover;
}

.modal__form {
  padding-left: 45px;
  padding-right: 45px;
}

.modal__form input {
  padding-bottom: 9px;
}

.modal__input-block {
  display: flex;
  flex-direction: column;
  margin-bottom: 36px;
}

.modal__input-block a {
  display: block;
  padding-top: 5px;
  width: max-content;
  align-self: flex-end;
  text-align: end;
  font-family: inherit;
  font-size: 12px;
  font-weight: inherit;
  color: #9b9997;
}

.modal__input-block a:visited {
  color: #9b9997;
}

.modal__input-block a:hover {
  color: #1d1613;
}

.modal__input-block a:active {
  opacity: 0.5;
}

.modal__submit {
  margin-top: 9px;
  margin-bottom: 15px;
  font-family: inherit;
}

.modal__footer {
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  color: #9b9997;
}

.modal__footer span {
  margin-right: 5px;
}

.modal__footer a {
  font: inherit;
  color: #1d1613;
}

.modal__footer a:visited {
  color: #1d1613;
}

.modal__footer a:hover {
  color: #9b9997;
}

.modal__footer a:active {
  opacity: 0.5;
}

.new h2 {
  margin-top: 0;
  margin-bottom: 25px;
}

.new__link {
  margin: 0 auto 60px;
}

.new__control {
  position: absolute;
  top: 40px;
}

.new__control span {
  display: none;
}

.new__control svg {
  width: 31px;
  height: 13px;
  stroke: #a87b62;
}

.new__control--previous {
  left: 0;
}

.new__control--next {
  right: 0;
  transform: rotate(180deg);
}

.new__control:hover svg {
  stroke: #1d1613;
}

.new__control:active svg {
  opacity: 0.5;
}

.new__catalog {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 30px;
  row-gap: 30px;
  margin-bottom: 52px;
  padding-left: 0;
}

.new__pagination {
  margin: 0 auto;
  width: max-content;
}

.new__pagination ul {
  padding-left: 0;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  min-height: 100%;
  text-size-adjust: none;
}

.page-body--opened-menu {
  overflow: hidden;
}

.page-body--opened-modal {
  overflow: hidden;
}

.page-body--opened-modal::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 80;
}

.page-body h2 {
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  font-size: 34px;
  letter-spacing: 0.03em;
  line-height: 130%;
}

.page-body ul {
  list-style: none;
  margin-top: 0;
}

.page-footer {
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-size: 16px;
  color: #ffffff;
  background-color: #000000;
}

.page-footer h2 {
  margin-top: 0;
  margin-bottom: 9px;
  font-family: "Cormorant Garamond", "Century Schoolbook L", "Georgia", serif;
  font-size: 34px;
  letter-spacing: 0.03em;
}

.page-footer p {
  margin-top: 0;
  margin-bottom: 37px;
  color: #9b9997;
}

.page-footer a {
  color: inherit;
}

.page-footer a:hover {
  color: #a87b62;
}

.page-footer a:active {
  color: inherit;
  opacity: 0.5;
}

.page-footer__container {
  display: grid;
  grid-template-columns: 43% 1fr max-content;
  column-gap: 7.5%;
  margin: 0 auto;
  padding-top: 90px;
  padding-bottom: 76px;
  padding-right: 98px;
  padding-left: 98px;
  width: 100%;
  max-width: 1366px;
}

.page-footer__subscribe input {
  padding-bottom: 9px;
}

.page-footer__navigation {
  display: grid;
  grid-template-columns: max-content max-content;
  column-gap: 19.1%;
}

.page-footer__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  padding-top: 4px;
  list-style: none;
  font-weight: 500;
}

.page-footer__list li {
  margin-bottom: 17px;
}

.page-header {
  margin-bottom: 9px;
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  color: #1d1613;
}

.page-header ul {
  list-style: none;
}

.page-header a:not(.page-header__logo) {
  color: inherit;
}

.page-header a:not(.page-header__logo):visited {
  color: inherit;
}

.page-header a:not(.page-header__logo):hover {
  border-bottom: 1px solid #a87b62;
}

.page-header a:not(.page-header__logo):active {
  border-style: none;
  opacity: 0.5;
}

.page-header__top-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 0 auto 50px;
  padding-left: 98px;
  padding-right: 98px;
  padding-top: 30px;
  max-width: 1366px;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
}

.page-header__button-menu {
  display: none;
}

.page-header__logo {
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.page-header__logo svg {
  width: 66px;
  height: 66px;
  fill: #a87b62;
}

.page-header__user-menu {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 12px;
  padding-left: 0;
  max-width: 45%;
}

.page-header__user-menu li:not(:last-child) {
  margin-right: 35px;
}

.page-header__cart svg {
  display: none;
}

.page-login {
  padding-top: 50px;
  padding-bottom: 20px;
  background-color: #f0eae4;
  width: 100%;
  height: 100%;
  grid-row: 1/-1;
}

.page-login__logo {
  margin: 0 auto 80px;
  order: -1;
}

.page-login__modal {
  display: flex;
  flex-direction: column;
  align-self: start;
}

.page-main {
  width: 100%;
}

.page-main--catalog {
  position: relative;
}

.page-main--catalog::after {
  content: "";
  position: absolute;
  bottom: 132px;
  width: 100%;
  height: 1px;
  background-color: #f6f5f4;
}

.page-main__section-full {
  margin-bottom: 120px;
}

.page-main__section {
  margin: 0 auto;
  padding-right: 98px;
  padding-left: 98px;
  width: 100%;
  max-width: 1366px;
  color: #1d1613;
}

.page-main__section--new {
  margin-bottom: 113px;
}

.page-main__section--blog {
  margin-bottom: 119px;
}

.page-main__section--faq {
  margin-bottom: 108px;
}

.page-main__section--gallery {
  margin-bottom: 131px;
}

.page-main__section--grid {
  display: grid;
  grid-template-columns: 250px 1fr;
  column-gap: 50px;
  margin-bottom: 74px;
}

.page-main__section h2 {
  text-align: center;
}

.page-main__container {
  position: relative;
}

.page-main__section-catalog {
  margin-bottom: 80px;
}

.pagination {
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
}

.pagination ul {
  display: flex;
}

.pagination p {
  display: none;
}

.pagination__item {
  font: inherit;
  color: #9b9997;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

.pagination__item:hover {
